.form-group {
  position: relative;
  color: #000;
  margin-bottom: 32px;
}
.form-group .control-label {
  position: absolute;
  pointer-events: none;
  left: 16px;
  top: 13px;
  font-size: 17px;
  font-weight: 300;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}
.form-group .bar {
  position: relative;
  display: block;
  width: 100%;
}
.form-group .bar:before,
.form-group .bar:after {
  content: '';
  height: 2px;
  width: 0;
  bottom: 0;
  position: absolute;
  background: #2c9aa2;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}
.form-group .bar:before {
  left: 50%;
}
.form-group .bar:after {
  right: 50%;
}
.form-group.has-error .bar:before,
.form-group.has-error .bar:after {
  background: #a94442;
}
.form-group.has-success .bar:before,
.form-group.has-success .bar:after {
  background: #3c763d;
}
.form-control {
  border: none;
  color: #000;
  background-color: #F9F9F9;
  border-bottom: 2px solid #8E8E8E;
  font-size: 17px;
  font-weight: 300;
  border-radius: 0;
}
.form-control:focus ~ .control-label,
.form-control:valid ~ .control-label {
  top: -24px;
  left: 0;
  font-size: 14px;
  font-weight: 700;
}
.form-control:focus {
  box-shadow: none;
  border-color: #8E8E8E;
}
.form-control:focus ~ .bar:before,
.form-control:focus ~ .bar:after {
  width: 50%;
}
.form-control::placeholder {
  color: #000000;
}
.control-label {
  margin-bottom: 0;
}
.error-message {
